/* chatroomliste........*/


.chatroomListeFrame{
    position: relative;
    width: 100%;
    height: calc(100% - 57px);
    overflow: scroll;
    overflow: -moz-scrollbars-none;
}
.chatroomListeFrame::-webkit-scrollbar { 
    display: none; 
}





