







.chatFrame{
    position: relative;
    width: 100%;
    height: calc(100% - 43px);

}






.chatListItem{
    width: 100%;
    height: auto;
    vertical-align: bottom;

}

.chatListVerlauf{
    width: 100%;
    height: 100%;
    scroll-snap-align: start;
    overflow: scroll;
    overflow: -moz-scrollbars-none;
}
.chatListVerlauf::-webkit-scrollbar { 
    display: none; 
}






/*
*
*
*
*  Div Spinner load old mess
*/

.SpinnerDivChatVerlauf{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    height: auto;
    background: none;
    width: 100%;
}
