


.profileScreen{
    position: relative;
    width: 100%;
    height: 100%;

}
.profileHead{
    position: relative;
    height: 30px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;


    
    

}

.profileBackBtn{
    position: relative;
    width: 95%;
    height: auto;
    background: none;
    outline: none;
    border: none;
    text-align: right;
}
.profileBackBackBtn{
    position: relative;
    width: 95%;
    height: auto;
    background: none;
    outline: none;
    border: none;
    text-align: left;
}

.profileMain{
    position: relative;
    height: calc(100% - 30px);
    width: 96%;
    margin-left: 2%;
    display: flex;
    align-items: center;
    flex-direction: column;

}


.profileImgDiv{
    position: relative;
    display: flex;
    justify-content: center;
    min-height: 150px;
    width: 50%;
    margin-bottom: 29px;
}
.profileImgBtn{
    position: relative;
    box-sizing: content-box;
    width: 100%;
    height: 100%;
    background: none;
    outline: none;
    border: none;


}

.profileDataDiv{
    position: relative;
    display: flex;
    width: 90%;
    height: 2.2em;
    background: none;

}

.profileBez{
    width: 9em;
    height: auto;
    background: none;

}

.profileInput{
    width: 60%;
    height: 2em;
    outline: none;
    padding-left: 5px;
    background: whitesmoke;
    border-radius: 50%;
    border: 1px solid rgb(203, 203, 204);
    color: black;

}

.profileSaveBtnDiv{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
}

.profileSaveBtn{
    width: 100%;
    min-height: 1.5em;
    margin-top: 20px;
    border: none;
    outline: none;
    background: none;
    font-weight: bold;
    font-size: 1.2em;
}
.profileSaveBtn:hover{
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
    color: rgb(144, 176, 164);
}


.profileSideBtn{
    width: 100%;
    margin-top: 50px;
    border: none;
    outline: none;
    background: none;
    font-style: oblique;
    color: black;
    text-align: right;
    vertical-align: middle;
    font-size: 1em;

}
.profileSideBtn:hover{
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
    color: rgb(31, 117, 255);
    
    
}



/*------------------------------ SIDE 2 -----------------------*/

.showColorFrame{
    position: relative;
    height: auto;
    width: 100%;
    margin-bottom: 1em;
    display: flex;
    justify-content: center;
    align-items: center;
}

.exampleChatDiv{
    position: relative;
    width: 95%;
    height: 95%;
    border-radius: 5px;
    border: 1px solid rgb(199, 199, 199);

}

.profileMainColor{
    position: relative;
    height: calc(100% - 340px);
    width: 96%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

}

.profileDataDivColor{
    position: relative;
    display: flex;
    width: 96%;
    height: 1em;
    background: none;

}

.profileBezColor{
    width: 19em;
    height: 1em;
    background: none;

}

.profileColorDiv{
    width: 100%;
    margin-left: 6px;
    display: flex;
    justify-content: center;
    align-items: center;

}

.profileColorBtn{
    position: relative;
    border: none;
    outline: none;
    height: 2em;
    width: 2em;
    border: 1px solid rgb(192, 192, 192);
    border-radius: 1em;

}
.profileColorBtn:hover{
    cursor: pointer;
    border-radius: 1em;
    border: 3px solid rgb(228, 228, 228) ;

}
.profileColorBtnState{
    position: relative;
    border: none;
    outline: none;
    height: 2em;
    width: 2em;
    border: 6px solid rgb(192, 192, 192);
    border-radius: 50%;
}
