






.chatroominListe{
    position: relative;
    width: 90%;
    margin-left: 5%;
    height: auto;
    display: flex;
    margin: 0.2em auto; 
    background: none;
    border-bottom: 1px solid rgb(233, 233, 233);
    
}

.btnchatroomuser{
    height: auto;
    width: 100%;
    font-size: 1.3em;
    display: flex;
    border: none;
    outline: none;
    text-align: left;
    background: none;
    color: rgb(90, 90, 90);
}

.btnchatroomuser:hover{
    cursor: pointer;
}


.chatroomuser{
    height: auto;
    width: 100%;
    position: relative;
    justify-content: center;
    background: none;
}

.chatroomuserDiv{
    width: 100%;
    height: 4em;
    display: flex;
    align-items: center;
    background: none;
 
}


.chatroomImgDiv{
    position: relative;
    box-sizing: content-box;
    min-width: 65px;
    min-height: 65px;
    height: 100%;
    max-height: 65px;
    max-width: 65px;
    margin: 0 auto;
    
}
.chatroomProfilImg{
    position: relative;
    width: 100%;
    height: 100%;

}

.chatroomSpecsDiv{
    width: 100%;
    height: 100%;
    display: block;
    background: none;
}


.chatroomTxtDivTop{
    height: 2em;
    width: auto;
    display: flex;
    margin-left: 5%;
    margin-right: 10%;
    justify-content: space-between;
    background: none;
}
.chatroomTxtDivBotton{
    height: 2em;
    width: auto;
    display: flex;
    margin-left: 5%;
    background: none;


}

.chatroomProfilName{
    height: 100%;
    width: auto;
    font-size: 1.2em;
    vertical-align: middle;
    background: none;
    overflow: hidden;
}

.underDateDiv{
    position: absolute;
    width: 50px;
    height: 50px;
    top: 0;
    z-index: 8;
    
}

.messageCountDiv{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    background: #2e96e0;
    color: white;
    font-size: 18px;
    border-radius: 50%;
    height: 30px;
    width: 30px;
    float: left;


}

.chatroomJoined{
    height: 1em;
    width: auto;
    border: 1px solid rgb(45, 151, 45);
    border-radius: 3px;
    text-align: center;
    color: rgb(45, 151, 45);
    font-size: 0.6em;
    margin-left: 1em;
    padding: 0.2em;
    background: none;

}
.chatroomChatName{
    height: auto;
    width: 100%;
    font-size: 0.5em;
    display: table-cell;
    vertical-align: bottom;
    color: rgb(138, 138, 138);

}

