







/*     rechte seite     */

.chat_colunm_left{
    position: relative;
    width: 97%;
    margin-left: 1%;
    height: 100%;
    display: flex;
    margin: 0.3em auto;  
    
}



.chatblase_left{
    outline: none;
    position: relative;
    min-width: 6em;
    max-width: 94%;
    height: auto;
    border: none;
    padding-bottom: 1em;
    border-bottom-right-radius: 0.5em;
    border-bottom-left-radius: 2px;
    border-top-left-radius: 0.5em;
    border-top-right-radius: 0.5em;
    background: white;
    word-wrap: break-word;
    color: black;
    white-space: pre;
    border: 1px solid rgba(34, 34, 34, 0.02);
    box-shadow: 2px 2px 1px 1px rgba(160, 160, 160, 0.05);
    
}

.chatblase_left:focus{
    background: rgb(31, 31, 31);
    color: white;
}

.chatMessage_left{
    width: auto;
    max-width: 94%;
    font-family: Arial, Helvetica, sans-serif;
    float: left;
    text-align: left;
    outline: none;
    resize: none;
    border: none;
    background: none;
    padding-top: 0.6em;
    font-size: 1.1em;
    margin-left: 0.8em;
    margin-right: 0.5em;
    white-space: pre;
    break-inside: auto;
    white-space: pre-line;
    font-family: medium-content-sans-serif-font,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;  

}

.chatdate_left{
    position: absolute;
    display: flex;
    left: 1.1em;
    bottom: 0.1em;
    font-size: 0.7em;
    color: rgba(71, 71, 71, 0.5);
}































