


/*--------- Suche-------------------*/
.searchFrame{
    position: relative;
    width: 100%;
    height: 50px;


}
.searchDiv{
    position: relative;
    width: auto;
    height: auto;
    
}

.searchInputDiv{
    width: 95%;
    margin-left: 2.5%;
    height: 40px;
    background: none;
    border-radius: 7px;
    border: 1px solid rgb(235, 233, 233);
    display: flex;
    justify-content: center;
    align-items: center;

}

.searchInput{
    position: relative;
    width: 90%;
    height: auto;
    outline: none;
    resize: none;
    border: none;
    background: none;
    font-size: 1.3em;
    margin-left: 20px;
    color: #2e96e0;


}
.searchInput::placeholder{
    text-align: center;
    font-size: 1.3em;
    color: rgb(233, 231, 231);

}

.searchIconBtn{
    width: 10%;
    height: auto;
    background: none;
    border: none;
    outline: none;
    color:  rgb(233, 231, 231);
}
.searchIconBtn:hover{
    color: #2e96e0;
    cursor: pointer;
}




/*-------------search User add list---------*/

.searchFoundFrame{
    position: relative;
    width: 100%;
    height: auto;
    
}
.searchUserBtn{
    position: relative;
    width: 90%;
    margin-left: 5%;
    margin-top: 7px;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid grey;
    outline: none;
    border-radius: 7px;
    z-index: 5;
    color: black;
    box-shadow: none;

}

.searchUserImgDiv{
    position: relative;
    width: 40px;
    height: 40px;


}
.searchUserBtn:hover{
    background:  rgb(187, 187, 187);;
    cursor: pointer;

}

.searchUserImgDiv{
    position: relative;
    width: 190px;
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin: 0 auto;
    overflow: hidden;
    margin-top: 5px;
    margin-bottom: 5px;

}

.searchUserImg{
    position: relative;
    width: 100%;
    height: 100%;
    

}

.searchUserNameDiv{
    width: 100%;
    font-size: 1.5em;

}
.searchUserIcon{
    width: 100%;
    color: grey;
}